/** @jsx React.DOM */

var $ = require('jquery')
var _ = require('lodash')
window.React = require('react')

var Box = React.createClass({displayName: "Box",
	close: function () {
		this.props.close();
	},
	componentDidMount: function () {
		var self = this;
		$('body').on('keypress', function(e){
			if (e.which === 27){
				self.close();
			}
		});
	},
	render: function () {
		return (
			React.createElement("div", null, 
				React.createElement("div", {className: "modal-blackout", onClick: this.close, "data-action": "close-modal"}), 
				React.createElement("div", {className: "modal-box"}, 
					React.createElement("i", {className: "close-btn", onClick: this.close, "data-action": "close-modal"}), 
					this.props.children
				)
			)
		);
	}
});

var Dialog = module.exports = function (component, className, onRender, onClose) {
	var $el = $('<div class="modal">').appendTo("body");
	if (className) {
		$el.addClass(className);
	}
	function close () {
		$el.fadeOut();
		React.unmountComponentAtNode($el[0]);
		onClose && onClose($el[0], c);
	}
	component.props.close = close;
	var c = React.render(React.createElement(Box, {close: close}, component), $el[0],
		function () {
			// Defer execution, so variable c is set.
			setTimeout(function () {
				$el.fadeIn();
				onRender && onRender($el[0], c);
				$('body').focus();
			}, 10);
		});
}

var Tour = React.createClass({displayName: "Tour",
	render: function () {
		return (
			React.createElement("div", {className: ""}
			)
		);
	},
});

//

module.exports.TourDialog = function (data, onRender, onClose) {
	Dialog(
		Tour(data),
		"tour-modal",
		function (elm, component) {
			onRender && onRender.call(this, elm, component);
			app.pages.chop();
		},
		function (elm, component) {
			onClose && onClose.call(this, elm, component);
			app.pages.unchop();
		}
	);
};